.page {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background-color: @light;
  color: @text-color-basic;
  .reg-basic();
  font-size: 16px;
  line-height: 22px;
}

.page--noScroll {
  overflow: hidden;
}

.page__header {
  margin-bottom: 100px;
  @media (min-width: @tablet-width) {
    margin-top: 30px;
    margin-bottom: 36px;
  }
}


